<template>
  <div class="bg">
    <div class="container">
      <div class="row">
        <!--   , ,              -->
        <div class="col-md-12 min-vh-100 d-flex flex-column justify-content-center">
          <div class="row">
            <div class="col-lg-6 col-md-8 mx-auto">
              <div class="card rounded shadow shadow-sm mainbox">
                <div class="card-header">
                  <h5>91cm      </h5>
                </div>
                <div class="card-body">
                  <label v-if="!user.email">  </label>
                  <b-input v-if="!user.email" type="text" name="email" v-model="email" placeholder=""></b-input>
                  <label>91cm   </label>
                  <b-input type="text" name="name" v-model="user.name" placeholder=""></b-input>
                  <label>    </label>
                  <b-input type="text" name="phone" v-model="user.phone" placeholder=" "
                           @keyup="phoneFormatter" ref="phoneNum"></b-input>
                  <b-button class="float-right" style="margin:15px;" variant="primary" @click="insertUser"></b-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'SignUp',
    data() {
      return {
        user: {
          name: '',
          phone: '',
          email: '',
          picture: ''
        }
      }
    },
    created() {
      this.getUser()
    },
    methods: {
      phoneFormatter: function () {
        this.user.phone = this.user.phone.replace(/[^0-9]/g, "") //    
        this.user.phone = this.user.phone.replace(/(^02.{0}|^01.{1}|[0-9]{4})([0-9]+)([0-9]{4})/, "$1-$2-$3");//     
      },
      getUser() {
        this.$http.get('http://localhost:9191/api/user/getsession').then(res => {
          this.user.name = res.data.name
          this.user.phone = res.data.phone
          this.user.email = res.data.email
          this.email = this.user.email
          this.user.picture = res.data.picture
        })
      },
      insertUser() {
        this.user.email = this.email
        console.log(this.user)
        let csrfToken = document.cookie.match('(^|;) ?' + 'XSRF-TOKEN' + '=([^;]*)(;|$)')
        this.$http.post('http://localhost:9191/api/user/signup', JSON.stringify(this.user), {
          headers: {
            'X-CSRF-TOKEN': csrfToken[2],
            'Content-Type': 'application/json'
          }
        }).then(res => {
          if (res.data) {
            this.$router.replace('main')
          } else {
            this.$alertModal('error', ' ')
          }
        })
      }
    }
  }
</script>
<style>
  .bg {
    background-image: url("../assets/images/laptop.jpg");
  }
</style>
